import {
  IonButton,
  IonButtons,
  IonContent,
  IonHeader,
  IonIcon,
  IonMenuButton,
  IonTitle,
  IonToolbar,
  IonSlides,
  IonSlide
} from '@ionic/react';

import { download } from 'ionicons/icons';
import React from 'react';
import './Home.css';
import ReactGA from 'react-ga';

const initializeReactGA = () => {
  ReactGA.initialize(`${process.env.REACT_APP_TRACKING_ID}`);
  ReactGA.pageview('/home');
};
if (document.location.hostname !== 'localhost') {
  initializeReactGA();
}

// Optional parameters to pass to the swiper instance. See http://idangero.us/swiper/api/ for valid options.
const slideOpts = {
  initialSlide: 0,
  speed: 4000,
  loop: true,
  autoplay: {
    delay: 3500,
    disableOnInteraction: false
  },
  pagination: {
    el: '.swiper-pagination',
    type: 'bullets',
    clickable: true
  },
  direction: 'vertical'
};

const HomePage: React.FunctionComponent = () => {
  return (
    <>
      <IonHeader className='ion-hide-md-up'>
        <IonToolbar>
          <IonButtons slot='start'>
            <IonMenuButton />
          </IonButtons>
          <IonTitle className='animated fadeInLeft toolbar-title'>
            Sly Guard
          </IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent>
        <IonSlides
          pager={true}
          options={slideOpts}
          className='intro-slides'
          style={{
            backgroundImage: `url(${process.env.PUBLIC_URL}/assets/file.jpg)`
          }}
        >
          <IonSlide>
            <div className='intro-slides-text-box'>
              <h1 className='intro-slides-text'>Sly Guard</h1>
            </div>
          </IonSlide>
          <IonSlide>
            <div className='intro-slides-text-box'>
              <h1 className='intro-slides-text'>Executive Portection</h1>
            </div>
          </IonSlide>
          <IonSlide>
            <div className='intro-slides-text-box'>
              <h1 className='intro-slides-text'>Uniformed Guards</h1>
              <IonButton
                onClick={() =>
                  ReactGA.event({
                    category: 'User',
                    action: ''
                  })
                }
                href='/'
                fill='outline'
                color='dark'
              >
                <IonIcon slot='start' icon={download} />
                Get Started
              </IonButton>
            </div>
          </IonSlide>
        </IonSlides>
      </IonContent>
    </>
  );
};

export default HomePage;
